"use client";
import React, { useState } from "react";

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="bg-gray-900 border-b border-gray-800 sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Left: Logo */}
          <div className="flex items-center space-x-2">
            <div className="w-8 h-8 bg-purple-600 rounded-lg flex items-center justify-center">
              <span className="text-white font-bold text-lg">DS</span>
            </div>
            <span className="text-white text-lg font-semibold">
              Data Science Club
            </span>
          </div>

          {/* Center: Desktop Links */}
          <div className="hidden md:block">
            <div className="ml-10 flex items-center space-x-8">
              <a href="#" className="text-white hover:text-purple-400 transition-colors">
                Home
              </a>
              <a href="#" className="text-gray-300 hover:text-purple-400 transition-colors">
                Past Events
              </a>
              <a href="#" className="text-gray-300 hover:text-purple-400 transition-colors">
                Gallery
              </a>
              <a href="#" className="text-gray-300 hover:text-purple-400 transition-colors">
                Registration
              </a>
              <a href="#" className="text-gray-300 hover:text-purple-400 transition-colors">
                Contact
              </a>
            </div>
          </div>

          {/* Right: Auth Buttons */}
          <div className="hidden md:flex items-center space-x-4">
            <button className="text-gray-300 hover:text-white transition-colors">
              Log in
            </button>
            <button className="bg-purple-600 hover:bg-purple-700 text-white px-4 py-2 rounded-lg transition-colors">
              Sign up
            </button>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="text-gray-400 hover:text-white p-2"
            >
              {/* Hamburger Icon */}
              <svg
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
          </div>
        </div>

        {/* Mobile Dropdown Menu */}
        {isMenuOpen && (
          <div className="md:hidden border-t border-gray-800 mt-2">
            <div className="px-2 pt-2 pb-3 space-y-1">
              <a href="#" className="block text-white hover:bg-gray-800 rounded-md px-3 py-2">
                Home
              </a>
              <a href="#" className="block text-gray-300 hover:bg-gray-800 rounded-md px-3 py-2">
                Past Events
              </a>
              <a href="#" className="block text-gray-300 hover:bg-gray-800 rounded-md px-3 py-2">
                Gallery
              </a>
              <a href="#" className="block text-gray-300 hover:bg-gray-800 rounded-md px-3 py-2">
                Registration
              </a>
              <a href="#" className="block text-gray-300 hover:bg-gray-800 rounded-md px-3 py-2">
                Contact
              </a>

              {/* Mobile Auth Buttons */}
              <div className="border-t border-gray-800 pt-4 pb-3">
                <div className="flex flex-col space-y-2">
                  <button className="text-gray-300 hover:text-white text-left">
                    Log in
                  </button>
                  <button className="bg-purple-600 hover:bg-purple-700 text-white px-3 py-2 rounded-lg">
                    Sign up
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
